body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
div.todoItem{
  text-align: left;
  border-bottom: 2px dashed #4c4c4c;
}
.todoItem button{
  text-align: right;
}

.todoItem p{
  margin: 0;
  padding: 15px;
}
.completedItem{
  background: #b3ee48;
  text-decoration: line-through;
}
.delBtn{
  border: none;
  border-radius: 50%;
  background: red;
  color: white;
  padding: 8px 12px;
  float: right;
  font-weight: bold;
  cursor: pointer;
}
.headerStyle{
  background: #333;
  color: white;
  text-align: center;
  padding: 10px;
}
.formAddTodo{
  display: flex;
}
.formAddTodo input[type='text']{
  padding: 6px;
}
.btn{
  display: inline-block;
  border: none;
  background: #555;
  color: #fff;
  padding: 7px 20px;
  cursor: pointer;
}
.btn:hover{
  background: #666;
}
.link-style{
  color: white;
  text-decoration: none;
}
.checkbox{

}
